



























import Vue from 'vue'
import { auth } from '@/store'

export default Vue.extend({
  middleware: 'guest',
  data() {
    return {
      username: '',
      password: '',
      teste: ''
    }
  },
  methods: {
    async onSubmit() {
      try {
        await auth.create({
          username: this.username,
          password: this.password
        })
        this.$notify({
          type: 'success',
          text: 'Sucesso! Voc√™ ser√° redirecionado.'
        })
        setTimeout(() => this.$router.push('/'), 2000)
      } catch {
        this.$notify({
          type: 'error',
          text: 'Login Incorreto'
        })
      }
    }
  }
})
